import React, { useEffect, useRef } from 'react'
import gsap from 'gsap'
import { ScrollTrigger } from 'gsap/ScrollTrigger'

gsap.registerPlugin(ScrollTrigger)

const timeline = [
  { year: 1988, text: 'Início do grupo', description: 'Formação do grupo na periferia de São Paulo' },
  { year: 1990, text: 'Primeiros shows', description: 'Apresentações que marcaram o início da trajetória' },
  { year: 1997, text: 'Sobrevivendo no Inferno', description: 'Álbum que se tornou referência do rap nacional' },
  { year: 2002, text: 'Nada Como Um Dia...', description: 'Disco duplo que consolidou o legado' },
  { year: 2012, text: 'Shows internacionais', description: 'Expansão para além das fronteiras brasileiras' },
  { year: 2023, text: 'Legado vivo', description: 'Influência permanente na cultura brasileira' }
]

export default function About(){
  const sectionRef = useRef(null)
  const titleRef = useRef(null)
  const textRef = useRef(null)
  const timelineRefs = useRef([])

  useEffect(() => {
    const ctx = gsap.context(() => {
      // Animação do título
      if(titleRef.current) {
        gsap.fromTo(titleRef.current,
          { opacity: 0, y: 50, filter: 'blur(10px)' },
          {
            opacity: 1,
            y: 0,
            filter: 'blur(0px)',
            duration: 1,
            ease: 'power3.out',
            scrollTrigger: {
              trigger: titleRef.current,
              start: 'top 85%',
              toggleActions: 'play none none reverse'
            }
          }
        )
      }

      // Animação do texto
      if(textRef.current) {
        gsap.fromTo(textRef.current,
          { opacity: 0, x: -30 },
          {
            opacity: 1,
            x: 0,
            duration: 0.8,
            ease: 'power2.out',
            scrollTrigger: {
              trigger: textRef.current,
              start: 'top 85%',
              toggleActions: 'play none none reverse'
            }
          }
        )
      }

      // Animação da timeline
      timelineRefs.current.forEach((item, idx) => {
        if(item) {
          gsap.fromTo(item,
            { opacity: 0, x: 50, scale: 0.9 },
            {
              opacity: 1,
              x: 0,
              scale: 1,
              duration: 0.6,
              ease: 'power2.out',
              delay: idx * 0.15,
              scrollTrigger: {
                trigger: item,
                start: 'top 85%',
                toggleActions: 'play none none reverse'
              }
            }
          )
        }
      })
    }, sectionRef)

    return () => ctx.revert()
  }, [])

  return (
    <section 
      id="about" 
      ref={sectionRef}
      className="py-24 md:py-32 relative grain-bg overflow-hidden"
    >
      {/* Background decorativo */}
      <div className="absolute inset-0 opacity-5">
        <div className="absolute top-1/4 left-0 w-px h-1/2 bg-gradient-to-b from-gold via-transparent to-transparent"></div>
      </div>

      <div className="container relative z-10">
        <h2 
          ref={titleRef}
          className="text-4xl md:text-6xl lg:text-7xl font-heading text-gold mb-6 reveal"
        >
          A História
        </h2>
        
        <div className="mt-12 grid md:grid-cols-2 gap-12 lg:gap-16">
          <div ref={textRef} className="reveal">
            <p className="text-xl md:text-2xl leading-relaxed text-white/90 mb-6">
              Racionais MC's surgem como <span className="text-gold font-semibold">voz crítica</span> da periferia de São Paulo — sua música é <span className="text-red-light">documento social</span>, denúncia e poesia.
            </p>
            <p className="text-lg leading-relaxed text-white/70">
              Este perfil resume marcos e discografia selecionada, com imagens e álbuns que marcaram gerações e continuam influenciando a cultura brasileira.
            </p>
          </div>
          
          <div className="relative">
            {/* Linha vertical da timeline */}
            <div className="absolute left-6 top-0 bottom-0 w-0.5 bg-gradient-to-b from-gold via-gold/50 to-transparent"></div>
            
            <ul className="space-y-8">
              {timeline.map((item, idx) => (
                <li 
                  key={item.year}
                  ref={el => timelineRefs.current[idx] = el}
                  className="flex items-start gap-6 reveal relative pl-2"
                >
                  {/* Ponto da timeline */}
                  <div className="relative z-10 flex-shrink-0">
                    <div className="w-12 h-12 rounded-full bg-gradient-to-br from-gold to-gold-dark flex items-center justify-center shadow-lg shadow-gold/30 border-2 border-gold/50">
                      <div className="w-3 h-3 rounded-full bg-black"></div>
                    </div>
                  </div>
                  
                  <div className="flex-1 pt-1">
                    <div className="text-gold font-heading text-2xl mb-1">{item.year}</div>
                    <div className="text-white font-semibold text-lg mb-2">{item.text}</div>
                    <div className="text-white/60 text-sm">{item.description}</div>
                  </div>
                </li>
              ))}
            </ul>
          </div>
        </div>
      </div>
    </section>
  )
}
